nav {
    position: fixed;
    top: 0;
    width: 100%;
    height: auto;
    background-color: #282c34;
}

.nav-list {
    position: relative;
    list-style: none;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #282c34;
    padding: 10px 0;
}

.nav-list-item {
    margin-right: 15px;
}

.nav-list-item .link {
    text-decoration: none;
    cursor: pointer;
    color: white;
    padding: 10px;
    border-radius: 5%;
}

.btn {
    position: absolute;
    right: 10px;
    top: 7px;
    display: none;
    padding: 5px;
    font-size: 18px;
}

.btn-change-theme {
    position: absolute;
    right: 15px;
    top: 7px;
    padding: 5px;
    font-size: 18px;
    cursor: pointer;
}

@media screen and (max-width : 500px) {

    nav {
        height: 50px;
    }

    .nav-list {
        padding: 0;
        flex-direction: column;
        height: auto;
    }

    .nav-list-item:nth-child(1){
        border-top: 1px solid white;
        margin-top: 50px;
    }

    .nav-list-item {
        height: 70px;
        width: 100%;
        border-bottom: 1px solid white;
        line-height: 70px;
        text-align: center;
        margin-right: 0;
    }

    .btn {
        display: block;
    }

    .nav-list-close {
        display: none;
    }
}