.title-form {
    text-align: center;
    font-size: 50px;
}

.container-form {
    padding: 10px;
    max-width: 800px;
    margin: 30px auto 0;
    display: flex;
    flex-direction: column;
}

.container-form label {
    font-size: 20px;
}

.container-form input, .container-form textarea {
    padding: 10px;
    margin-bottom: 25px;
    font-size: 18px;
}

.container-form textarea {
    min-height: 200px;
    font-family: Arial, Helvetica, sans-serif;
    resize: none;
}

.container-form button {
    padding: 10px 15px;
    width: 200px;
    max-width: 200px;
    background-color: #282c34;
    color: white;
    border: none;
    cursor: pointer;
}